import React, { useEffect, useState } from 'react';
import styles from './Gallery.module.css';
import Lightbox from 'react-image-lightbox';
import 'react-image-lightbox/style.css';

const Gallery: React.FC = () => {
    const images = Array.from({ length: 24 }, (_, index) => `gallery-${index + 1}.jpg`);
    const [photoIndex, setPhotoIndex] = useState(-1);
    const [isOpen, setIsOpen] = useState(false);
    const [loaded, setLoaded] = useState(false);
    const [scrollPosition, setScrollPosition] = useState(0); // 현재 스크롤 위치를 저장하는 상태

    const handleOpenLightbox = (index: number) => {
        if (!isOpen) {
            setScrollPosition(window.pageYOffset);
            setPhotoIndex(index);
            setIsOpen(true);
        }
    };

    const preventDefault = (e: TouchEvent) => {
        e.preventDefault();
    };

    useEffect(() => {
        if (isOpen) {
            // 페이지의 스크롤을 막고 현재 위치에 고정
            document.body.style.position = 'fixed';
            document.body.style.top = `-${scrollPosition}px`;
            document.body.style.width = '100%';
            document.addEventListener('touchmove', preventDefault, { passive: false });
        } else {
            // Lightbox가 닫힐 때 원래 위치로 복원
            document.body.style.position = '';
            document.body.style.top = '';
            document.body.style.width = '';
            window.scrollTo(0, scrollPosition);
            document.removeEventListener('touchmove', preventDefault);
        }

        return () => {
            // 컴포넌트가 언마운트 될 때도 원래 위치로 복원
            document.body.style.position = '';
            document.body.style.top = '';
            document.body.style.width = '';
            document.removeEventListener('touchmove', preventDefault);
            window.scrollTo(0, scrollPosition);
        };
    }, [isOpen, scrollPosition]);

    return (
        <div className={styles.Gallery}>
            <div className={styles.pageTitle}>GALLERY</div>
            <div className={styles.PhotoContainerRow}>
                <div className={styles.GridBoxRow}>
                    {images.map((image, index) => (
                        <div key={index} className={styles.gridItemRow}
                            onClick={() => handleOpenLightbox(index)}
                        >
                            <img src={`images/${image}`} alt={`Gallery Image ${index + 1}`} />
                        </div>
                    ))}
                </div>
            </div>
            {isOpen && (
                <>
                    <style>
                        {`
                            .ril__navButtons {
                                display: none;
                            }
                            .ril__zoomInButton {
                                display: none !important;
                            }
                            .ril__zoomOutButton {
                                display: none !important;
                            }
                        `}
                    </style>
                    <Lightbox
                        mainSrc={`images/${images[photoIndex]}`}
                        nextSrc={`/images/${images[(photoIndex + 1) % images.length]}`}
                        prevSrc={`/images/${images[(photoIndex + images.length - 1) % images.length]}`}
                        enableZoom={true}
                        zoomInLabel=''
                        zoomOutLabel=''
                        onImageLoad={() => {
                            setLoaded(true);
                        }}
                        onCloseRequest={() => {
                            setIsOpen(false);
                            setLoaded(false);
                            setPhotoIndex(-1);
                        }}
                        onMovePrevRequest={() => {
                            setPhotoIndex((photoIndex + images.length - 1) % images.length);
                            setLoaded(false);
                        }}
                        onMoveNextRequest={() => {
                            setPhotoIndex((photoIndex + 1) % images.length)
                            setLoaded(false);
                        }}
                        reactModalStyle={{
                            overlay: {
                                backgroundColor: 'black'
                            }
                        }}
                        toolbarButtons={[]}
                    />
                </>
            )}
        </div>
    );
}

export default Gallery;
