import React from 'react';
import styles from './Footer.module.css';
import { Button, Divider } from '@mui/material';

const Footer: React.FC = () => {
    const handleCopy = () => {
        navigator.clipboard.writeText(window.location.href); // 복사할 계좌번호
        alert('주소가 복사되었습니다.');
    }
    const handleShare = () => {
        Kakao.Share.sendDefault({
            objectType: 'feed',
            content: {
                title: '백승태 ♥ 이영은의 결혼식에 초대합니다',
                description: '2024년 10월 5일 (토) 12:30pm\n시은소교회 2층 본당',
                imageUrl: `${window.location.origin}/images/main.jpg?t=${new Date().getTime()}`,
                link: {
                    mobileWebUrl: window.location.href,
                    webUrl: window.location.href,
                },
            },
            buttons: [
                {
                    title: '모바일 청첩장 보기',
                    link: {
                        mobileWebUrl: window.location.href,
                        webUrl: window.location.href,
                    },
                }
            ],
        });
    };
    return (
        <div className={styles.Footer}>
            <Button
                variant="contained"
                onClick={handleShare}
                sx={{
                    color: '#ffffff',
                    padding: '2vw 1vw',
                    fontSize: '4vw',
                    fontWeight: 600,
                    backgroundColor: '#666666',
                    width: '50%',
                    textTransform: 'none',
                    boxShadow: 'none',
                    ":active": {
                        backgroundColor: '#666666',
                        borderColor: '#666666',
                        color: '#ffffff'
                    },
                    ":hover": {
                        backgroundColor: '#666666',
                        borderColor: '#666666',
                        color: '#ffffff'

                    }
                }}
            >
                TALK
            </Button>
            <div style={{ display: 'flex', flexDirection: 'row', alignItems: 'center', height: '3vw' }}>
                <Divider orientation='vertical' flexItem sx={{ width: '1vw', height: '3vw', color: 'white', marginLeft: 3, marginRight: 3, borderColor: 'white' }} />
            </div>
            <Button
                variant="contained"
                onClick={handleCopy}
                sx={{
                    color: '#ffffff',
                    padding: '2vw 1vw',
                    fontSize: '4vw',
                    fontWeight: 600,
                    backgroundColor: '#666666',
                    width: '50%',
                    textTransform: 'none',
                    boxShadow: 'none',
                    ":active": {
                        backgroundColor: '#666666',
                        borderColor: '#666666',
                        color: '#ffffff'
                    },
                    ":hover": {
                        backgroundColor: '#666666',
                        borderColor: '#666666',
                        color: '#ffffff'
                    }
                }}
            >
                주소 복사
            </Button>
        </div>
    );
}

export default Footer;
