import React, { useEffect, useState } from 'react';
import styles from './Map.module.css';
import { Button, Divider } from '@mui/material';

const Map: React.FC = () => {

    useEffect(() => {
        const container = document.getElementById('map');
        if (container) {  // container가 null이 아닌지 확인
            const options = {
                center: new kakao.maps.LatLng(37.2935276397531, 127.05123587041953), // 시은소교회 좌표
                level: 5
            };

            const map = new kakao.maps.Map(container, options);

            const markerPosition = new kakao.maps.LatLng(37.2935276397531, 127.05123587041953);
            const marker = new kakao.maps.Marker({
                position: markerPosition
            });

            marker.setMap(map);
        }
    }, []);

    return (
        <div className={styles.Map}>
            <div className={styles.pageTitle}>LOCATION</div>
            {/* <div style={{ textAlign: 'center', fontSize: '4vw', fontWeight: '600', lineHeight: '10vw' }}>오시는길</div> */}
            <div style={{ textAlign: 'center', fontSize: '4vw', fontWeight: '100', lineHeight: '10vw' }}>
                경기 수원시 영통구 센트럴타운로 100번길 33
            </div>
            <div style={{ textAlign: 'center', fontSize: '4vw', fontWeight: '100', lineHeight: '10vw' }}>
                시은소교회 2층 본당
            </div>
            <Button
                variant="outlined" // 외곽선 버튼 스타일
                sx={{
                    display: 'block',
                    margin: '20px auto',
                    borderRadius: '20px',
                    padding: '5px 15px',
                    fontSize: '1rem',
                    color: '#555',
                    borderColor: '#ddd',
                    textTransform: 'none',
                    '&:active': {
                        backgroundColor: 'transparent', // active 시 배경색 변화 없음
                        borderColor: '#ddd', // active 시 테두리 색 변화 없음
                    },
                }}

                onClick={() => {
                    navigator.clipboard.writeText('경기 수원시 영통구 센트럴타운로 100번길 33');
                    alert('주소가 복사되었습니다.');
                }}
            >
                주소 복사
            </Button>
            <div id="map" style={{ width: '90%', height: '50vw', marginTop: '10%' }}></div>
            <div style={{
                display: 'flex',
                flexDirection: 'row',
                justifyContent: 'space-between',
                backgroundColor: '#f3f3ea',
                alignItems: 'center',
                width: '90%',
                height: '15vw',
                boxSizing: 'border-box',
                padding: '3vw',
            }}>
                <Button
                    className={styles.apiButtons}
                    onClick={() => {
                        window.location.href = 'https://map.naver.com/p/entry/place/11596082?placePath=%2Fhome&c=15.00,0,0,0,dh';
                    }}>
                    <img src='/icons/naver.png' alt='kakaonavi' style={{ width: '4vw', height: 'auto', marginRight: '2vw' }}></img>
                    네이버 지도
                </Button>
                <div style={{ display: 'flex', flexDirection: 'row', alignItems: 'center', height: '3vw' }}>
                    <Divider orientation='vertical' flexItem sx={{ width: '1vw', height: '3vw', color: 'white', marginLeft: 1, marginRight: 1, borderColor: 'black' }} />
                </div>
                <Button
                    className={styles.apiButtons}
                    onClick={() => {
                        Kakao.Navi.start({
                            name: '시은소 교회',
                            x: 127.05123587041953,
                            y: 37.2935276397531,
                            coordType: 'wgs84',
                        });
                    }}>
                    <img src='/icons/kakaonavi.png' alt='kakaonavi' style={{ width: '4vw', height: 'auto', marginRight: '2vw' }}></img>
                    카카오내비
                </Button>
                <div style={{ display: 'flex', flexDirection: 'row', alignItems: 'center', height: '3vw' }}>
                    <Divider orientation='vertical' flexItem sx={{ width: '1vw', height: '3vw', color: 'white', marginLeft: 1, marginRight: 1, borderColor: 'black' }} />
                </div>
                <Button
                    className={styles.apiButtons}
                    // onClick={handleTMapNavigation}>
                    onClick={() => {
                        if (navigator.geolocation) {
                            navigator.geolocation.getCurrentPosition(
                                position => {
                                    const startX = position.coords.longitude;
                                    const startY = position.coords.latitude;
                                    const endX = '127.05123587041953';
                                    const endY = '37.2935276397531';
                                    const goalName = '시은소 교회';
                                    const startName = '현재 위치';

                                    const tmapRouteUrl = `tmap://route?goalx=${endX}&goaly=${endY}&goalname=${encodeURIComponent(goalName)}&startx=${startX}&starty=${startY}&startname=${encodeURIComponent(startName)}`;
                                    window.location.href = tmapRouteUrl;
                                },
                                error => {
                                    console.error('Error getting location:', error);
                                    alert('현재 위치를 가져올 수 없습니다.');
                                }
                            );
                        }
                    }}>
                    <img src='/icons/tmap.png' alt='tmap' style={{ width: '4vw', height: 'auto', marginRight: '2vw' }}></img>
                    T맵
                </Button>
            </div>
            <div >
                {/* <>
                    <div className={styles.transport}>
                        <div style={{ borderRadius: '50%', border: '1px solid #a8a8a8', width: '6vw', height: '6vw', display: 'flex', justifyContent: 'center', alignItems: 'center', marginRight: '2vw' }}>
                            <img src='/icons/bus.svg' alt='phone icon' width='60%' height='60%' />
                        </div>
                        버스
                    </div>
                    <Divider orientation="horizontal" variant="middle" sx={{ borderColor: '#a8a8a8', width: '90vw', marginTop: '4vw', marginBottom: '4vw', borderStyle: 'dashed' }} />
                    <div className={styles.transportContent} style={{ marginTop: '3.7vw', fontSize: '3.7vw' }}>
                        (7-1, 7-2, 9-2, 13-4, 18, 80, 81, 88-1)
                    </div>
                    <div className={styles.transportContent} style={{ marginTop: '2vw' }}>
                        광교카페거리 · 대림e편한세상 · 광교오드카운티 · 연무중학교 앞 하차
                    </div>
                    <div style={{ display: 'flex', justifyContent: 'center' }}>
                        <Divider orientation="horizontal" variant="middle" sx={{ borderColor: '#000000', width: '1vw', marginTop: '4vw', marginBottom: '4vw', borderStyle: 'dashed' }} />
                    </div>
                    <div className={styles.transportContent} style={{ marginTop: '3.7vw', fontSize: '3.7vw' }}>
                        (60, 660, 700-2, 720-1, 720-2, 721, 730, 3007, 4000)
                    </div>
                    <div className={styles.transportContent} style={{ marginTop: '2vw' }}>
                        광교역사공원 ·광교카페거리 앞 하차
                    </div>
                    <div style={{ display: 'flex', justifyContent: 'center' }}>
                        <Divider orientation="horizontal" variant="middle" sx={{ borderColor: '#000000', width: '1vw', marginTop: '4vw', marginBottom: '4vw', borderStyle: 'dashed' }} />
                    </div>
                    <div className={styles.transportContent} style={{ marginTop: '3.7vw', fontSize: '3.7vw' }}>
                        (사당역 M5422, 강남역 M5422)
                    </div>
                    <div className={styles.transportContent} style={{ marginTop: '2vw' }}>
                        광교테크노벨리 ·푸르지오월드마크 앞 하차
                    </div>
                </> */}
                <div className={styles.transport}>
                    <div style={{ borderRadius: '50%', border: '1px solid #a8a8a8', width: '6vw', height: '6vw', display: 'flex', justifyContent: 'center', alignItems: 'center', marginRight: '2vw' }}>
                        <img src='/icons/metro.svg' alt='phone icon' width='60%' height='60%' />
                    </div>
                    지하철
                </div>
                <Divider orientation="horizontal" variant="middle" sx={{ borderColor: '#a8a8a8', width: '90vw', marginTop: '4vw', marginBottom: '4vw', borderStyle: 'dashed' }} />
                <div className={styles.transportContent}>
                    신분당선 [광교중앙역 3번 출구] 도보 7분
                </div>
                <div className={styles.transport}>
                    <div style={{ borderRadius: '50%', border: '1px solid #a8a8a8', width: '6vw', height: '6vw', display: 'flex', justifyContent: 'center', alignItems: 'center', marginRight: '2vw' }}>
                        <img src='/icons/car.svg' alt='phone icon' width='60%' height='60%' style={{ marginLeft: '0.3vw' }} />
                    </div>
                    자가용
                </div>
                <Divider orientation="horizontal" variant="middle" sx={{ borderColor: '#a8a8a8', width: '90vw', marginTop: '4vw', marginBottom: '4vw', borderStyle: 'dashed' }} />
                <div className={styles.transportContent}>
                    네비게이션 :  교회명 "시은소교회" 혹은
                    <br />전화번호 "031 253 1004" 입력
                    <br />
                    주소검색 : 수원시 영통구 센트럴타운로100번길 33
                </div>
                <div className={styles.transport}>
                    <div style={{ borderRadius: '50%', border: '1px solid #a8a8a8', width: '6vw', height: '6vw', display: 'flex', justifyContent: 'center', alignItems: 'center', marginRight: '2vw' }}>
                        <img src='/icons/parking.svg' alt='phone icon' width='60%' height='60%' style={{ marginLeft: '0.5vw' }} />
                    </div>
                    주차
                </div>
                <Divider orientation="horizontal" variant="middle" sx={{ borderColor: '#a8a8a8', width: '90vw', marginTop: '4vw', marginBottom: '4vw', borderStyle: 'dashed' }} />
                <div className={styles.transportContent}>
                    시은소교회 건물 옆, 주차동에 주차
                </div>
            </div>
        </div>
    );
}

export default Map;
