import React, { useRef, useState } from 'react';
import styles from './Invitation.module.css';
import { Button, ClickAwayListener, Dialog, Divider } from '@mui/material';

const Invitation: React.FC = () => {
    const [openContact, setOpenContact] = useState(false);
    const buttonRef = useRef<HTMLButtonElement>(null);

    const handleOpenContact = () => {
        setOpenContact(true);
    };

    const handleCloseContact = () => {
        setOpenContact(false);
        if (buttonRef.current) {
            buttonRef.current.focus(); // 포커스를 버튼으로 되돌림
        }
    };

    return (
        <>
            <div className={styles.invitation}>
                <div className={styles.pageTitle}>INVITATION</div>
                {/* <div style={{ marginTop: '10%', textAlign: 'center', fontSize: '4vw', fontWeight: '600', lineHeight: '10vw' }}>
                    ♡Our Wedding Day♡
                </div> */}
                <div className={styles.content}>
                    <pre style={{ fontFamily: '"Nanum Myeongjo", serif' }}>
                        아름다운 하늘 아래
                        <br />
                        저희 둘이 하나되어 사랑을 약속합니다.
                        <br />
                        <br />
                        '마음을 같이하여 같은 사랑을 가지고
                        <br />
                        뜻을 합하며 한마음을 품어' (빌2:2)
                        <br />
                        살아가겠습니다.
                        <br />
                        <br />
                        같은 곳을 향해 걸어가는 첫 여정에
                        <br />
                        동행하여 축복해주시면 감사하겠습니다.
                    </pre>
                </div>
                <div className={styles.family}>
                    <div style={{ fontWeight: 600 }}>
                        백봉현
                    </div>
                    <div style={{
                        margin: '0px 2vw',
                        width: '0.5vw',
                        height: '0.5vw',
                        borderRadius: '50%',
                        backgroundColor: '#000000'
                    }} />
                    <div style={{ fontWeight: 600 }}>
                        김춘실
                    </div>
                    <div style={{
                        fontSize: '3vw',
                        margin: '0px 3vw 0px 1vw'
                    }}>
                        의 &nbsp;장남
                    </div>
                    <div style={{ fontWeight: 600 }}>
                        승태
                    </div>
                </div>
                <div className={styles.family}>
                    <div style={{ fontWeight: 600 }}>
                        이광식
                    </div>
                    <div style={{
                        margin: '0px 2vw',
                        width: '0.5vw',
                        height: '0.5vw',
                        borderRadius: '50%',
                        backgroundColor: '#000000'
                    }} />
                    <div style={{ fontWeight: 600 }}>
                        조현정
                    </div>
                    <div style={{
                        fontSize: '3vw',
                        margin: '0px 3vw 0px 1vw'
                    }}>
                        의 &nbsp;장녀
                    </div>
                    <div style={{ fontWeight: 600 }}>
                        영은
                    </div>
                </div>
                <Button
                    ref={buttonRef}
                    sx={{
                        backgroundColor: 'white',
                        width: '50%',
                        border: '2.5px solid #F7F7F7',
                        borderRadius: '10px',
                        marginTop: '5vw',
                        padding: '1vw'
                    }}
                    onClick={handleOpenContact}
                >
                    <div style={{ fontSize: '4vw', color: 'black' }}>연락하기</div>
                </Button>
            </div >
            <div className={styles.waveContainer}>
                <img src='/icons/wave.svg' alt='wave' width='100%' height='100%' />
            </div>
            <Dialog style={{ padding: '2vw' }}
                PaperProps={{
                    style: {
                        width: '100%',
                        margin: 0,
                    },
                }}
                open={openContact}
                onClose={handleCloseContact}
            >
                <ClickAwayListener onClickAway={handleCloseContact} >
                    <div className={styles.contact}>
                        <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-around' }}>
                            <div style={{ display: 'flex', flexDirection: 'column' }}>
                                <div className={styles.contactItem}>
                                    <div>
                                        신랑
                                    </div>
                                    <div style={{ fontWeight: 600 }}>
                                        백승태
                                    </div>
                                    <div className={styles.contactIcon}>
                                        <a href='tel:01095303534'>
                                            <img src='/icons/phone.svg' alt='phone icon' width='24' height='24' />
                                        </a>
                                        <a href='sms:01095303534'>
                                            <img src='/icons/sms.svg' alt='phone icon' width='24' height='24' />
                                        </a>
                                    </div>
                                </div>
                                <div className={styles.contactItem} style={{ letterSpacing: '-0,5vw', marginTop: '10vw', marginBottom: '10vw' }}>
                                    <div>
                                        신랑 아버지
                                    </div>
                                    <div style={{ fontWeight: 600 }}>
                                        백봉현
                                    </div>
                                    <div className={styles.contactIcon}>
                                        <a href='tel:01098545533'>
                                            <img src='/icons/phone.svg' alt='phone icon' width='24' height='24' />
                                        </a>
                                        <a href='sms:01098545533'>
                                            <img src='/icons/sms.svg' alt='phone icon' width='24' height='24' />
                                        </a>
                                    </div>
                                </div>
                                <div className={styles.contactItem}>
                                    <div>
                                        신랑 어머니
                                    </div>
                                    <div style={{ fontWeight: 600 }}>
                                        김춘실
                                    </div>
                                    <div className={styles.contactIcon}>
                                        <a href='tel:01067247767'>
                                            <img src='/icons/phone.svg' alt='phone icon' width='24' height='24' />
                                        </a>
                                        <a href='sms:01067247767'>
                                            <img src='/icons/sms.svg' alt='phone icon' width='24' height='24' />
                                        </a>
                                    </div>
                                </div>
                            </div>
                            <div style={{ display: 'flex', flexDirection: 'column' }}>
                                <div className={styles.contactItem}>
                                    <div>
                                        신부
                                    </div>
                                    <div style={{ fontWeight: 600 }}>
                                        이영은
                                    </div>
                                    <div className={styles.contactIcon}>
                                        <a href='tel:01040189344'>
                                            <img src='/icons/phone.svg' alt='phone icon' width='24' height='24' />
                                        </a>
                                        <a href='sms:01040189344'>
                                            <img src='/icons/sms.svg' alt='phone icon' width='24' height='24' />
                                        </a>
                                    </div>
                                </div>
                                <div className={styles.contactItem} style={{ letterSpacing: '-0,5vw', marginTop: '10vw', marginBottom: '10vw' }}>
                                    <div>
                                        신부 아버지
                                    </div>
                                    <div style={{ fontWeight: 600 }}>
                                        이광식
                                    </div>
                                    <div className={styles.contactIcon}>
                                        <a href='tel:01040389344'>
                                            <img src='/icons/phone.svg' alt='phone icon' width='24' height='24' />
                                        </a>
                                        <a href='sms:01040389344'>
                                            <img src='/icons/sms.svg' alt='phone icon' width='24' height='24' />
                                        </a>
                                    </div>
                                </div>
                                <div className={styles.contactItem}>
                                    <div>
                                        신부 어머니
                                    </div>
                                    <div style={{ fontWeight: 600 }}>
                                        조현정
                                    </div>
                                    <div className={styles.contactIcon}>
                                        <a href='tel:01040289344'>
                                            <img src='/icons/phone.svg' alt='phone icon' width='24' height='24' />
                                        </a>
                                        <a href='sms:01040289344'>
                                            <img src='/icons/sms.svg' alt='phone icon' width='24' height='24' />
                                        </a>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </ClickAwayListener>
            </Dialog>
        </>
    );
}

export default Invitation;
